<template>
    <div class="dropzone-wrapper clearfix"
         ref="dropzone"
         @click="handleWrapperClick"
         @dragover.prevent
         @dragenter="handleDragEnter"
         @dragleave="handleDragLeave"
         @drop.prevent="handleDrop">
        <slot v-if="!uploadList.length"></slot>
        <ul class="preview-list" @click.stop v-else>
            <li class="preview-wrapper" v-for="(item, index) in previewList">
                <figure>
                    <transition name="fly-top">
                        <div class="progress" v-show="item.isUploaded"></div>
                    </transition>
                    <img :src="item.data">
                    <figcaption>
                        <h5>{{ uploadList[index].name }}</h5>
                        <a class="close" @click="handleRemove(index)">移除({{ fileSize(uploadList[index].size) }})</a>
                    </figcaption>
                </figure>
            </li>
        </ul>
        <input type="file" name="file" ref="fileInput" multiple="multiple" style="display: none;"
               @change="handleChange">
        <div class="btn-group">
            <input type="button" value="上传" @click.stop="handleUpload" v-if="uploadList.length && !allUpload">
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
  const FILE_PREVIEW = 'data:image/jpeg;base64,';

  export default {
    props: {
      url: {
        type: String,
        required: true
      },
      limitInMB: {
        type: Number,
        default: 5
      },
      limitFiles: {
        type: Number,
        default: 10
      },
      method: {
        type: String,
        default: 'post'
      },
      headers: {
        type: Object,
        default: function () {
          return {};
        }
      }
    },

    data() {
      return {
        uploadList: [],
        previewList: [],
        allUpload: false,
        FILE_SIZE_LIMIT: 1024 * 1024 * this.limitInMB // 5MB
      };
    },

    methods: {
      fileReader(data, cbk) {
        let fr = new FileReader();
        fr.onload = (ev) => {
          cbk && cbk(ev.target.result);
        };
        fr.readAsDataURL(data);
      },
      handleWrapperClick() {
        this.$refs.fileInput.click();
      },
      handleChange(ev) {
        for (let file of ev.target.files) {
          if (file.size > this.FILE_SIZE_LIMIT) {
            alert(`文件${file.name},超出大小限制！`);
          } else if (file.type.indexOf('image') === -1) {
            this.previewList.push({data: FILE_PREVIEW, isUploaded: false});
            this.uploadList.push(file);
          } else {
            this.fileReader(file, (data => {
              if (this.uploadList.length >= this.limitFiles) {
                alert(`当前最多只能上传${this.limitFiles}个文件`);
                return;
              }
              this.previewList.push({data, isUploaded: false});
              this.uploadList.push(file);
            }));
          }
        }
      },
      handleRemove(index) {
        this.uploadList.splice(index, 1);
        this.previewList.splice(index, 1);
      },
      handleDrop(ev) {
        this.$emit('onDrop', ev);
        for (let file of ev.dataTransfer.files) {
          if (this.uploadList.some(e => e.name === file.name)) return;
          if (file.size > this.FILE_SIZE_LIMIT) {
            alert(`文件${file.name},超出大小限制！`);
          } else if (file.type.indexOf('image') === -1) {
            this.previewList.push({data: FILE_PREVIEW, isUploaded: false});
            this.uploadList.push(file);
          } else {
            this.fileReader(file, (data => {
              if (this.uploadList.length >= this.limitFiles) {
                alert(`当前最多只能上传${this.limitFiles}个文件`);
                return;
              }
              this.previewList.push({data, isUploaded: false});
              this.uploadList.push(file);
            }));
          }
        }
      },
      handleUpload() {
        if (!this.uploadList.length) {
          alert('请选择上传文件！');
          return;
        }
        if (!this.url) {
          throw new Error('url is undefined')
        }
        let count = this.uploadList.length;
        let success = 0;
        this.uploadList.forEach(e => {
          let formData = new FormData();
          formData.append('file', e);
          let xhr = new XMLHttpRequest();
          xhr.open(this.method, this.url, true);
          for (let key in this.headers) {
            xhr.setRequestHeader(key, this.headers[key]);
          }
          xhr.onload = () => {
            if (xhr.status >= 200 && xhr.status < 207) {
              this.previewList[success].isUploaded = true;
              this.$emit('onProgress', success++ / count, e);
              if (success === count) {
                this.allUpload = true;
                this.$emit('onSuccess', JSON.parse(xhr.response));
              }
            } else {
              this.$emit('onError', xhr);
            }
          };
          xhr.send(formData);
        });
      },
      handleDragEnter(ev) {
        this.$emit('onDragenter', ev);
      },
      handleDragLeave(ev) {
        this.$emit('onDragleave', ev);
      },
      fileSize(size) {
        if (size / 1024 / 1024 < 1) {
          return (size / 1024).toFixed(2) + 'KB';
        } else {
          return (size / 1024 / 1024).toFixed(2) + 'MB';
        }
      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
    @import url('./css/reset.css');

    *, *::after, *::before {
        box-sizing: border-box;
    }

    .dropzone-wrapper {
        position: relative;
        width: 100%;
        padding: 40px 30px;
        border: 4px dashed #cbb3c2;
        font-size: 40px;
        color: #d1d1d1;
        user-select: none;
        transition: .5s;
        background-color: #fff;
        cursor: pointer;
        font-family: 'Avenir', "PingFang SC", "Microsoft YaHei", Helvetica, "宋体", sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        &:hover {
            background-color: #e8fffd;
        }
        .preview-wrapper {
            position: relative;
            float: left;
            width: 250px;
            margin-top: 10px;
            transition: .4s;
            font-size: 20px;
            figure {
                position: relative;
                overflow: hidden;
                width: 250px;
                height: 250px;
                img {
                    width: 250px;
                    height: 250px;
                    transition: transform .4s;
                }
                figcaption {
                    width: 100%;
                    height: 80px;
                    padding: 10px;
                    transform: translateY(0);
                    background-color: #2c3f50;
                    color: #ed4466;
                    opacity: 0;
                    transition: transform .4s, opacity .3s;
                    h5 {
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        text-align: left;
                    }
                    .close {
                        display: block;
                        margin-top: 5px;
                        border: 1px solid #eee;
                        background-color: #fff;
                        transition: .4s;
                        &:hover {
                            background-color: #636363;
                            color: #fff;
                        }
                    }
                }
                .progress {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: #56ffa1;
                    opacity: .2;
                }
            }

            &:hover {
                figure {
                    img {
                        transform: translateY(-30px);
                    }
                    figcaption {
                        transform: translateY(-100%);
                        opacity: 1;
                    }
                }
            }
        }
        .btn-group {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-40px);
            input {
                font-size: 20px;
                width: 80px;
                border: 1px solid gray;
                transition: .4s;
                &:hover {
                    border-color: #fff;
                    background-color: gray;
                    color: #fff;
                }
            }
        }
    }

    .fly-top-enter-active, .fly-top-leave-active {
        transition: transform .2s cubic-bezier(.55, .53, .25, .94);
    }

    .fly-top-enter, .fly-top-leave-active {
        transform: translate3d(0, -100%, 0);
    }
</style>


